import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';

export default function ProjectCreate() {
    const [projectTitle, setProjectTitle] = useState('');
    const [projectDescription, setProjectDescription] = useState('');
    const [plUser, setPlUser] = useState('');
    const [devUser, setDevUser] = useState('');
    const [testUser, setTestUser] = useState('');
    const [email, setEmail] = useState('');
    const [id, setId] = useState('');
    const [modify, setModify] = useState(false);
    const navigate = useNavigate();

    const handleCreate = () => {
        const newProject = {
            projectTitle,
            projectDescription,
            plUser: plUser.split(',').map(user => user.trim()),
            devUser: devUser.split(',').map(user => user.trim()),
            testUser: testUser.split(',').map(user => user.trim())
        };
        console.log('Project 생성됨!', newProject);
        // 여기에 프로젝트 생성 API 호출 로직을 추가할 수 있습니다.
        navigate('/');
    };

    return (
        <Paper sx={{ width: '90%', margin: 'auto', padding: 4 }}>
            <Box sx={{ display: 'flex', flexDirection: 'column', mt: 4 }}>
                <Typography variant="h6" sx={{ mb: 2, fontWeight: 'bold' }}>아이디 (선택)</Typography>
                <TextField
                    value="cksdn63"
                    variant="standard"
                    margin="normal"
                    fullWidth
                    InputProps={{
                        readOnly: true,
                    }}
                />

                <Typography variant="h6" sx={{ mt: 2, fontWeight: 'bold' }}>이메일 (필수)</Typography>
                <TextField
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    variant="standard"
                    margin="normal"
                    fullWidth
                />

                <FormControlLabel
                    control={
                        <Checkbox
                            checked={modify}
                            onChange={(e) => setModify(e.target.checked)}
                            color="primary"
                        />
                    }
                    label="수정"
                />

                <Typography variant="h6" sx={{ mt: 2, fontWeight: 'bold' }}>MYBOX 사용 중인 ID (필수)</Typography>
                <TextField
                    placeholder="현재 사용 중인 ID를 기재해 주세요. (개설되어 이용 중인 ID로 로그인 후 문의해 주셔야 정확한 확인이 가능합니다.)"
                    variant="outlined"
                    margin="normal"
                    fullWidth
                />

                <Typography variant="h6" sx={{ mt: 2, fontWeight: 'bold' }}>이전 받으려는 ID (필수)</Typography>
                <TextField
                    placeholder="이전을 받고자 하는 ID를 기재해 주세요."
                    variant="outlined"
                    margin="normal"
                    fullWidth
                />

                <Typography variant="h6" sx={{ mt: 2, fontWeight: 'bold' }}>문의 내용 (선택)</Typography>
                <TextField
                    placeholder="구체적인 내용을 기재해 주세요."
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    multiline
                    rows={4}
                />

                <Button variant="contained" color="primary" sx={{ mt: 3, backgroundColor: '#03C75A', '&:hover': { backgroundColor: '#03C75A' } }}>
                    완료
                </Button>
            </Box>
        </Paper>
    );
};
